//버튼 이벤트 메서드
//1.조회
async function onSearch(page) {
  console.log('조회 버튼 클릭')
  try {
    const response = await axios.get('/INSUR9101/Get_Sawaon', {
      params: {
        headcd: selectedHead.value,
        positioncd: selectedPosition.value,
        timcd: selectedTim.value,
        jik: selectedjik.value,
        induceCD : induceCD.value,
        outYN : selectedStatus.value,
        saknm : saKnm.value,
        Take : pageSize.value,
        Skip : page
      }
    })
    rowData.value = response.data.data
    ExcelData.value = response.data.exceldata
    totalRecords.value = response.data.totalCount;  // 백엔드에서 총 개수 전달 필요
    currentPage.value = page;
  } catch (error) {
    console.error('조회실패:', error)
  }
}
//2.삭제
async function DeleteRow(){
  console.log('삭제버튼클릭')
  console.log(selectedgridrow.value.SA_CD)
  try{
    const delete_Itmes = {
      COMP_CD: '00001',
      SA_CD: selectedgridrow.value.SA_CD,   
    }
    const response = await axios.delete('/INSUR9101/Delete', {
      data : delete_Itmes,
      headers:{
        'Content-Type' : 'application/json'
      }, withCredentials: true 
    })
    console.log('삭제성공',response)
    onSearch(1)
  }catch(error){
    console.error('삭제실패 : ',error)
  }
}
//3.액샐
