onMounted(async () => {
  // 첫 마운트 시 권역 데이터 로드
  try {
    // 권한 및 세션 데이터 로드
    await loadAuth();

    // 초기 디테일 권한에 따른 콤보박스 선택 및 disable 설정
    await initializeSelections();

  } catch (error) {
    console.error(error)
  } finally {
    // 모든 데이터 로드 완료 후 로딩 상태 변경
    isLoading.value = false;
  }
})

