<template>
  <div>
    <div 
        class="grid-pagination-container"
        :style="{ height: `calc(${pageHeight}px - ${pagin_Bool ? 65 : 0}px)` }"
    > 
      <table class="result-table">
          <thead>
              <tr>
                  <th 
                      v-for="(col, index) in columns" 
                      :key="col.field"
                      :colspan="col.colspan || 1"
                      :rowspan="col.rowspan || 1"
                      :style="{ 
                          minWidth: col.width ? `${col.width}px`  : '150px',
                          maxWidth: col.width ? `${col.width}px` : {},
                          width: col.width ? `${col.width}px` : {},
                          ...getHeaderStyle(index) 
                      }"
                  >
                      {{ col.headerName }}
                  </th>
              </tr>

              <!-- 두 번째 행 (subHeaders) -->
              <tr v-if="columns.some(col => col.subHeaders)">
                <th 
                  v-for="(subcol, subcolindex) in columns.filter(col => col.subHeaders).flatMap(col => col.subHeaders)"
                  :key="subcol.field"
                  :colspan="subcol.colspan || 1"
                  :rowspan="subcol.rowspan || 1"
                  :style="{ 
                          minWidth: subcol.width ? `${subcol.width}px`  : '150px',
                          maxWidth: subcol.width ? `${subcol.width}px` : {},
                          width: subcol.width ? `${subcol.width}px` : {},
                          ...getHeaderStyle(subcolindex) 
                  }"
                >
                  {{ subcol.headerName }}
                </th>
              </tr>
          </thead>
          <tbody>
            <tr v-for="(row, row_index) in data" 
                :key="row_index" 
                @dblclick="handleRowDblClick(row)"
                @click="selectRow(row, row_index)">
              
              <template v-for="(col, index) in columns">
                <!-- 부모 컬럼이 subHeaders가 있으면 그 안의 필드들을 출력 -->
                <template v-if="col.subHeaders">
                  <td v-for="(subCol) in col.subHeaders"
                      :key="subCol.field"
                      :style="{ 
                          minWidth: subCol.width ? `${subCol.width}px` : {},
                          maxWidth: subCol.width ? `${subCol.width}px` : {},
                          width: subCol.width ? `${subCol.width}px` : {},
                          ...getColumnStyle(index) 
                      }"
                      :class="{
                          'selected-row': row_index === selectRow_Index,
                          'left_text': subCol.textAlign?.toLowerCase() === 'left',
                          'right_text': subCol.textAlign?.toLowerCase() === 'right',
                          'center_text': !subCol.textAlign || subCol.textAlign?.toLowerCase() === 'center'
                      }">
                    {{ row[subCol.field] }}
                  </td>
                </template>

                <!-- 일반 컬럼 출력 -->
                <td v-else
                    :key="col.field"
                    :style="{ 
                          minWidth: col.width ? `${col.width}px` : {},
                          maxWidth: col.width ? `${col.width}px` : {},
                          width: col.width ? `${col.width}px` : {},
                          ...getColumnStyle(index) 
                    }"
                    :class="{
                        'selected-row': row_index === selectRow_Index,
                        'left_text': col.textAlign?.toLowerCase() === 'left',
                        'right_text': col.textAlign?.toLowerCase() === 'right',
                        'center_text': !col.textAlign || col.textAlign?.toLowerCase() === 'center'
                    }">
                  {{ row[col.field] }}
                </td>
              </template>
            </tr>
          </tbody>
      </table>
    </div>
    <div v-show="pagin_Bool">
      <!-- 페이징 영역 -->
      <div class="pagination">
            <button @click="changePageGroup(-1)" :disabled="startPage === 1">◀</button>
        
            <button 
                v-for="page in visiblePages" 
                :key="page" 
                @click="changePage(page)" 
                :class="{ active: currentPageLocal === page }">
                {{ page }}
            </button>
        
            <button @click="changePageGroup(1)" :disabled="startPage + 9 >= totalPages">▶</button>
        
            <!-- <input 
            type="number" 
            v-model.number="jumpToPage" 
            @keydown.enter="jumpToSpecificPage" 
            min="1" 
            :max="totalPages" />
            <button @click="jumpToSpecificPage">이동</button> -->
        </div>
    </div>
  </div>
</template>

<script setup>
import { computed, ref, watch,defineProps, defineEmits  } from 'vue'

const emit = defineEmits(['pageChange', 'rowDblclick','rowSelected'])

const props = defineProps({
    columns: {
        type: Array,
        required: true
    },// 컬럼정보
    data: {
        type: Array,
        required: true
    },//로우 데이터
    totalRecords: {
        type: Number,
        required: true
    },//전체 데이터 개수
    pagin_Bool: {
        type: Boolean,
        default: false,
    },//페이징 여부
    pageSize: {
        type: Number,
        default: 100
    },//페이지 사이즈
    currentPage: {
        type: Number,
        default: 1
    },//현재 페이지
    onSearch: {
        type: Function,
        required: true
    },// 부모의 onSearch 함수를 prop으로 전달받음
    fixedColumnCount: { 
        type: Number,
        default: 0
    },// ✅ 부모로부터 받을 고정 컬럼 개수
    pageHeight: {
        type: String,
        default: "1000"
    }
})

//선택된row
const selectedRow = ref(null)
const selectRow_Index = ref(-1);

//행 클릭시 실행
function selectRow(row, index){
    selectedRow.value = row;
    selectRow_Index.value = index;
    emit('rowSelected', row)
}

// 선택된 행인지 확인하는 헬퍼 함수 (고유 식별자 SA_CD를 사용)
// function isSelected(row) {
//   return selectedRow.value && selectedRow.value.SA_CD === row.SA_CD
// }

// 독립적인 fixedColumnCount 유지을위한 변수
const localFixedColumnCount = ref(props.fixedColumnCount);


// 부모 값 변경 시 반응형으로 업데이트
watch(() => props.fixedColumnCount, (newVal) => {
  localFixedColumnCount.value = newVal;
});


// ✅ 개별 컬럼 스타일을 `:style`로 직접 적용하는 방식으로 변경 Header
const getHeaderStyle = (index) => {
  if (index < localFixedColumnCount.value) {
    let leftValue = props.columns.slice(0, index).reduce((sum, col) => sum + (col.width ? col.width : 150), 0);
    return {
      left: `${leftValue}px`, // ✅ `col.width`에 따라 `left` 값 동적으로 설정
      position: 'sticky',
      background: 'rgb(242, 242, 242)', // ✅ 고정 헤더 배경색 적용
      zIndex: '4' // ✅ th가 td보다 높아야 함
    };
  }
  return {};
};



// ✅ 개별 컬럼 스타일을 `:style`로 직접 적용하는 방식으로 변경 RowData
const getColumnStyle = (index) => {
  if (index < localFixedColumnCount.value) {
    // ✅ 이전 컬럼들의 `width` 합을 계산하여 `left` 값 설정
    let leftValue = props.columns.slice(0, index).reduce((sum, col) => sum + (col.width ? col.width : 150), 0);
    return {
      left: `${leftValue}px`, // ✅ `col.width`에 따라 `left` 값 동적으로 설정
      position: 'sticky',
      background: '#fff',
      zIndex: '3'
    };
  }
  return {};
};

// 로컬 페이지 상태 (부모의 currentPage와 동기화)
const currentPageLocal = ref(props.currentPage)
watch(
    () => props.currentPage,
    (newVal) => {
        currentPageLocal.value = newVal
    }
)

// 전체 페이지 수 계산
const totalPages = computed(() => Math.ceil(props.totalRecords / props.pageSize))

// 페이지 그룹 관련 (한 화면에 최대 10개의 페이지 버튼 표시)
const startPage = ref(1)
const visiblePages = computed(() => {
    return Array.from(
        { length: Math.min(10, totalPages.value - startPage.value + 1) },
        (_, i) => startPage.value + i
    )
})

// 점프할 페이지 번호
// const jumpToPage = ref(null)

function changePage(page) {
    if (page < 1 || page > totalPages.value) return
    currentPageLocal.value = page
    props.onSearch(page)
}

function changePageGroup(direction) {
    const newStart = startPage.value + direction * 10
    if (newStart < 1 || newStart > totalPages.value) return
    startPage.value = newStart
}

// function jumpToSpecificPage() {
//     const pageNum = parseInt(jumpToPage.value)
//     if (pageNum >= 1 && pageNum <= totalPages.value) {
//         currentPageLocal.value = pageNum
//         startPage.value = Math.floor((pageNum - 1) / 10) * 10 + 1
//         emit('pageChange', pageNum)
//     } else {
//         alert("페이지 범위를 초과하였습니다.")
//     }
// }

function handleRowDblClick(row) {
    emit('rowDblclick', row);
}

</script>

<style scoped>
/* @import '../assets/Css/main.css'; */

.selected-row{
    background-color: var(--selected-row-bg, rgb(244,244,117)) !important;
}

.grid-pagination-container  {
  font-family: sans-serif;
  margin: 0;
  position: relative; 
  width: 100%;
  overflow-x: auto; /* ✅ 스크롤 가능하게 설정 */
}

.result-table {
  width: 100%;
  border-collapse: collapse; /* ✅ border를 자연스럽게 맞추기 위함 */
  table-layout: auto !important; /* ✅ auto로 설정하여 서브헤더에도 width 적용 */
  margin: 0;
  clip-path: inset(0); /* 부모의 경계를 명확히 설정 */
  border-collapse: separate; /* ✅ 테이블 보더가 병합되지 않도록 설정 */
  border-spacing: 0; /* ✅ 셀 간격 제거 (선이 두 개 생기는 문제 방지) */
  overflow-y: auto; /* ✅ 스크롤 가능하게 설정 */
}

.result-table th {
  font-size: 20px;
  width: 150px;
  height: 50px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: rgb(242, 242, 242);
  border: 1px solid rgb(210, 210, 210);
  background-clip: padding-box;
  position: sticky;
  top: 0;         /* 스크롤 시 상단 고정 */
  z-index: 3;
  cursor: col-resize; /* (리사이즈 커서 표시 – 실제 기능은 별도 구현 필요) */
  top: -1px;
}

.result-table td {
  font-size: 20px;
  width: 150px;
  height: 40px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border: 1px solid rgb(210, 210, 210);
  background-clip: padding-box;
  position: relative;
  /* text-align: center; */
}

/* 6. (선택 사항) 고정 헤더 셀이 다른 셀보다 위에 보이도록 z-index 추가 */
.result-table th {
  z-index: 3;
}

.pagination {
  position: sticky; /* ✅ 스크롤 시에도 왼쪽 하단에 고정 */
  left: 0;
  bottom: 0;
  width: fit-content;
  background: white; /* ✅ 스크롤 시 배경 유지 */
  padding: 5px;
  z-index: 5; /* ✅ 다른 요소 위에 표시 */
  display: flex; /* ✅ 버튼이 가로로 정렬되도록 설정 */
  align-items: center;
}

.pagination button {
  width: 50px;
  height: 50px;
  background-color: #f0f0f0;
  color: #333;
  border: 1px solid #ccc;
  padding: 5px 10px;
  margin: 0 2px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

/* 활성화된 버튼에 대한 스타일 */
.pagination button.active {
  background-color: #007bff; /* 파란색 배경 */
  color: #fff;              /* 흰색 글자 */
  font-weight: bold;        /* 볼드체 */
  border-color: #007bff;
}

/* 버튼에 마우스를 올렸을 때의 효과 (선택 사항) */
.pagination button:hover {
  background-color: #0056b3;
  color: #fff;
}


.pagination input{
  margin-left: 1px;
  margin-right: 1px;
  height: 49.5px;
  width: 120px;
}

</style>