// 드래그 관련 상태
const modalRef = ref(null);
const position = ref({ 
  top: window.innerHeight / 2 - 390, 
  left: window.innerWidth / 2 - 450,
});
let drag = { active: false, offsetX: 0, offsetY: 0 };


//모달닫기
const closeModal = () => {
  emit("update:isOpen", false);
  console.log('모달닫힘');
};

// 드래그 시작
const startDrag = (event) => {
  drag.active = true;
  drag.offsetX = event.clientX - modalRef.value.getBoundingClientRect().left;
  drag.offsetY = event.clientY - modalRef.value.getBoundingClientRect().top;

  window.addEventListener("mousemove", onDrag);
  window.addEventListener("mouseup", stopDrag);
};

// 드래그 중
const onDrag = (event) => {
  if (!drag.active) return;
  position.value.top = event.clientY - drag.offsetY;
  position.value.left = event.clientX - drag.offsetX;
};

// 드래그 종료
const stopDrag = () => {
  drag.active = false;
  window.removeEventListener("mousemove", onDrag);
  window.removeEventListener("mouseup", stopDrag);
};


//부모정보가 바뀌면 로컬도 업데이트
watch (
  () => props.sawonData,
  (newVal) => {
    localDate.value = {...newVal}
  },
  { deep: true,immediate: true }
)

// 모달이 열릴 때 (isOpen가 true가 될 때) 초기 옵션을 수동으로 불러오기
watch(() => props.isOpen, async (newVal) => {
  const tempPosition = localDate.value.selectedPosition;
  const tempTim = localDate.value.selectedTim;
  if (newVal) {
    // 만약 이미 selectedHead가 채워져 있다면 본부 데이터 불러오기
    if (localDate.value.selectedHead !=='') {
      try {
        const posResponse = await axios.get('/INSUR9101/getPosition', {
          params: { headcd: localDate.value.selectedHead }
        });
        positionList.value = posResponse.data;
      } catch (error) {
        console.error("본부 데이터 로딩 실패:", error);
      }
    }
    localDate.value.selectedPosition = tempPosition;
    // 만약 이미 selectedPosition가 채워져 있다면 팀 데이터 불러오기
    if (localDate.value.selectedPosition !=='') {
      try {
        const timResponse = await axios.get('/INSUR9101/getTim', {
          params: { positioncd: localDate.value.selectedPosition }
        });
        timList.value = timResponse.data;
      } catch (error) {
        console.error("팀 데이터 로딩 실패:", error);
      }
    }
    localDate.value.selectedTim = tempTim;
  }
});

//권역 데이터 변경 시 본부 데이터 로드
watch(()=>localDate.value.selectedHead, async (newHead) => {
  localDate.value.selectedPosition = ''
  positionList.value = []
