//카카오맵 api
function showApi(){
  new window.daum.Postcode({
    //주소선택후 실행될 함수 
    oncomplete: (data) => {
      //data.roadAddress(도로명 주소)와 기타 주소 정보를 가져옴.
      let fullRoadAddr = data.roadAddress;
      let extraRoadAddr = '';
      console.log('api 불러옴')
      //data.bname과 data.buildingName을 확인하여  주소를 보강. ex) bname과 = (만덕동) buildingName을(삼성아파트)
      if (data.bname !== '' && /[동|로|가]$/g.test(data.bname)) {
        extraRoadAddr += data.bname;
      }
      if (data.buildingName !== '' && data.apartment === 'Y') {
        extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
      }
      if (extraRoadAddr !== '') {
        extraRoadAddr = ' (' + extraRoadAddr + ')';
      }
      if (fullRoadAddr !== '') {
        fullRoadAddr += extraRoadAddr;
      }

      //우편번호 저장
      localDate.value.ZIP = data.zonecode;

      //상세주소 저장
      localDate.value.ADDR = fullRoadAddr;
    }
  }).open();
} 

