<template>
  <div class="tab-container">
    <div class="tabs" v-if="tabs.length > 0">
      <div 
        v-for="(tab, index) in tabs" 
        :key="index" 
        :class="{ active: activeTab === index }"
        class="tab"
        @click="changeTab(index)"
      > 
        {{ tab.name }}
        <span class="close-btn" @click.stop="closeTab(index)">✖</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabComponent",
  props: {
    tabs: {
      type: Array,
      required: true,
      default: () => []
    },
    activeTab: {
      type: Number,
      required: true
    }
  },
  methods: {
    changeTab(index) {
      this.$emit("changeTab", index);
    },
    closeTab(index) {
      this.$emit("closeTab", index);
    }
  }
};
</script>

<style scoped>
.tab-container {
  display: flex;
  border-bottom: 2px solid #ddd;
}

.tabs {
  display: flex;
  overflow-x: auto;
}

.tab {
  padding: 10px 20px;
  cursor: pointer;
  background: #f4f4f4;
  margin-right: 5px;
  border: 1px solid #ddd;
  border-radius: 5px 5px 0 0;
  position: relative;
}

.tab.active {
  background: white;
  border-bottom: none;
  font-weight: bold;
}

.close-btn {
  margin-left: 10px;
  cursor: pointer;
  color: red;
  font-size: 14px;
}
</style>
