<script>
    // import {ref} from 'vue';
    import {ref, onMounted, computed, onUnmounted } from 'vue';
    import axios from 'axios';

    export default {
        name : "MainPageView",
        components:{
        },
        setup(){

            const user_data = ref(null);

            const notice_List = ref(["1.jpg", "2.jpg", "3.jpg"]); // 공지 이미지 리스트
            const notice_translateX = ref(0); // 현재 슬라이드 위치
            const currentSlide = ref(0); // 현재 활성화된 슬라이드 인덱스
            const isPlaying = ref(true); // 자동 슬라이드 재생 여부

            const notice_slideWidth = 1103.41; // 슬라이드 하나의 너비
            const notice_speed = 3000; // 3초마다 이동
            let noticeInterval = null;

            // ✅ 다음 슬라이드로 이동
            const nextSlide = () => {
                currentSlide.value = (currentSlide.value + 1) % notice_List.value.length;
                updateSlide();
            };
    
            // ✅ 이전 슬라이드로 이동
            const prevSlide = () => {
                currentSlide.value = (currentSlide.value - 1 + notice_List.value.length) % notice_List.value.length;
                updateSlide();
            };

            // ✅ 특정 슬라이드로 이동
            const goToSlide = (index) => {
                currentSlide.value = index;
                updateSlide();
            };

            // ✅ 슬라이드 이동 함수
            const updateSlide = () => {
                notice_translateX.value = -currentSlide.value * notice_slideWidth;
            };

            // ✅ 자동 슬라이드 실행
            const notice_startAutoSlide = () => {
                if (!isPlaying.value) return; // 재생 상태가 아닐 때는 실행 X
                noticeInterval = setInterval(() => {
                    nextSlide();
                }, notice_speed);
            };

            // ✅ 자동 슬라이드 중지
            const notice_stopAutoSlide = () => {
                clearInterval(noticeInterval);
            };

            // ✅ 재생/일시정지 버튼 토글
            const toggleAutoSlide = () => {
                isPlaying.value = !isPlaying.value;
                if (isPlaying.value) {
                    notice_startAutoSlide();
                } else {
                    notice_stopAutoSlide();
                }
            };




            const insur_info_list = ref([]);
            const insur_translateX = ref(0); // 이동 거리
            let   insur_animationFrame = null;
            const insur_slideWidth = 150; // 슬라이드 하나의 너비
            const insur_speed = 1; // 이동 속도 (px/ms 단위)

            //  12개씩 나누고 부족한 개수는 `null`로 채우는 함수
            // const chunkArray = (data, chunkSize = 12) => {
            //     const chunks = [];
            //     for (let i = 0; i < data.length; i += chunkSize) {
            //         let chunk = data.slice(i, i + chunkSize);
            //         if (chunk.length < chunkSize) {
            //             chunk = [...chunk, ...Array(chunkSize - chunk.length).fill(null)]; // 🚀 빈 박스 자동 추가
            //         }
            //         chunks.push(chunk);
            //     }
            //     return chunks;
            // };

            //  `SUR01` 필터링 및 12개씩 분할
            // const filteredInsur01 = computed(() => chunkArray(insur_info_list.value?.filter(item => item.insur_cd === "SUR01") || []));
            //  `SUR02` 필터링 및 12개씩 분할
            // const filteredInsur02 = computed(() => chunkArray(insur_info_list.value?.filter(item => item.insur_cd === "SUR02") || []));

            const getNoticeImageSrc = (imageName) => {
                return `/Img/test/${imageName}`;
            };

            //  슬라이드 배열을 2배로 늘려서 자연스럽게 무한 루프 효과 적용
            const insur_extendedList = computed(() => [...insur_info_list.value, ...insur_info_list.value]);

            //  이미지 경로 설정
            const getInsurImageSrc = (imageName) => {
                return `/Img/common/${imageName}`;
            };

            const openLink = (url) => {
                if (url) {
                    window.open(url, '_blank'); // 새 탭에서 링크 열기
                }
            };

            const yearMontn = ref('');
            const now  = new Date();
            const year = now.getFullYear();  // 현재 년도
            const month = now.getMonth() + 1;  // 현재 월 (0부터 시작하므로 +1)
            const month_Perf = ref([
                ['구분','건수','보험료','S환산','구분', 'background-color: rgba(240, 240, 240, 1)'],
                ['생보','','','','',''],
                ['손보','','','','',''],
                ['자동차','','','','',''],
                ['일반','','','','',''],
                ['합계','','','','','background-color: rgba(240, 240, 240, 1)']
            ]);

            //보험사 링크 및 ID
            const insurance_Link_Id = ref([
                ["ABL생명", "DB생명", "IM라이프", "IBK연금","KB라이프"],
                ["신한라이프", "푸본현대"],
                ["AIG손보","DB손보"]
            ]);

            yearMontn.value = `${year}-${month < 10 ? '0' + month : month}`;

            const today = new Date();
            const currentYear = ref(today.getFullYear());
            const currentMonth = ref(today.getMonth());
            const currentToday = ref(today.getDay());

            // 📅 요일 배열 (일~토)
            const weekDays = ["일", "월", "화", "수", "목", "금", "토"];

            // 📅 현재 월의 첫 번째 날 요일 계산
            const firstDayOfMonth = computed(() => {
            return new Date(currentYear.value, currentMonth.value, 1).getDay();
            });

            // 📅 현재 월의 총 일수 계산
            const daysInMonth = computed(() => {
            return new Date(currentYear.value, currentMonth.value + 1, 0).getDate();
            });

            // 📅 첫 주 앞쪽 빈 칸 개수 계산
            const blankDaysStart = computed(() => {
            return Array.from({ length: firstDayOfMonth.value }, (_, i) => i);
            });

            // 📅 마지막 주 빈 칸 개수 계산 (6주가 되도록 자동 채우기)
            const blankDaysEnd = computed(() => {
                const totalCells = blankDaysStart.value.length + daysInMonth.value;
                const remainingCells = 42 - totalCells; // 6주(6 × 7) = 42칸 유지
                return Array.from({ length: remainingCells }, (_, i) => i);
            });


            //  부드럽게 무한 슬라이드 이동
            const insur_moveSlide = () => {
                insur_translateX.value -= insur_speed; // 왼쪽으로 조금씩 이동

                //  왼쪽으로 다 이동하면 순간적으로 리셋하여 끊김 없이 반복
                if (Math.abs(insur_translateX.value) >= insur_slideWidth * insur_info_list.value.length) {
                    insur_translateX.value = 0;
                }

                // `requestAnimationFrame()`을 사용하여 부드러운 애니메이션 적용
                insur_animationFrame = requestAnimationFrame(insur_moveSlide);
            };

            //  자동 슬라이드 시작
            const insur_startAutoSlide = () => {
                if (!insur_animationFrame) insur_moveSlide();
            };
    
            //  마우스를 올리면 멈추기
            const insur_stopAutoSlide = () => {
                if (insur_animationFrame) cancelAnimationFrame(insur_animationFrame);
                insur_animationFrame = null;
            };


            onMounted(async () => {
                try{
                    const response = await axios.get('http://localhost:5062/api/MainPageGetData', { withCredentials: true });
                    
                    console.log(response.data);
                    user_data.value = response.data.user;
                    insur_info_list.value = response.data.inuser_info; 
                    
                    notice_startAutoSlide();
                    insur_startAutoSlide(); 
                } catch(error){
                    alert(error.response.data.message);
                }
            });

            // ✅ 컴포넌트가 언마운트될 때 자동 슬라이드 정리
            onUnmounted(() => {
                insur_stopAutoSlide();
            });


            return{
                notice_List,
                notice_translateX,
                isPlaying,
                currentSlide,
                getNoticeImageSrc,
                nextSlide, 
                prevSlide, 
                goToSlide, 
                updateSlide, 
                notice_startAutoSlide,
                notice_stopAutoSlide, 
                toggleAutoSlide,
                yearMontn,
                month_Perf,
                insurance_Link_Id,
                // filteredInsur01,
                // filteredInsur02,
                insur_extendedList,
                insur_translateX,
                getInsurImageSrc,
                insur_startAutoSlide,
                insur_stopAutoSlide,
                openLink,
                user_data,
                currentYear,
                currentMonth,
                currentToday,
                weekDays,
                daysInMonth,
                blankDaysStart,
                blankDaysEnd,
            };
        }
    }
</script>