<template>
    <div v-if="isOpen">
      <div class="calendar-container">
        <!-- 📌 기본 달력 헤더 -->
        <div v-if="mode === 'calendar'" class="calendar-header">
          <button @click="prevMonth">←</button>
          <span class="daymonth" @click="toggleMonthView">{{ formattedMonth }}</span>
          <button @click="nextMonth">→</button>
          <button @click="closeCalendar">X</button>
        </div>

        <!-- 📌 월 선택 모드 헤더 -->
        <div v-else-if="mode === 'month'" class="calendar-header">
          <button @click="prevYear">←</button>
          <span class="daymonth"  @click="toggleYearView">{{ formattedYear }}</span>
          <button @click="nextYear">→</button>
          <button @click="closeMonthView">X</button>
        </div>

        <!-- 📌 년 선택 모드 헤더 -->
        <div v-else-if="mode === 'year'" class="calendar-header">
          <button @click="prevDecade">←</button>
          <span>{{ yearRange }}</span>
          <button @click="nextDecade">→</button>
          <button @click="closeMonthView">X</button>
        </div>

        <!-- 📌 요일 (기본 모드) -->
        <div v-if="mode === 'calendar'" class="calendar-weekdays">
          <div v-for="(day, index) in daysOfWeek" :key="index"
               :class="{ 'sunday': index === 0, 'saturday': index === 6 }">
            {{ day }}
          </div>
        </div>

        <!-- 📌 날짜 선택 (기본 모드) -->
        <div v-if="mode === 'calendar'" class="calendar-grid">
          <div v-for="day in calendarDays" 
               :key="day.date"
               class="calendar-day"
               :class="{ 
                  'selected': isSelected(day.date), 
                  'faded': day.isOtherMonth,
                  'sunday': day.date.day() === 0, 
                  'saturday': day.date.day() === 6 
                }"
               @click="selectDate(day.date)">
            {{ day.day }}
          </div>
        </div>

        <!-- 📌 월 선택 모드 -->
        <div v-else-if="mode === 'month'" class="calendar-grid_month">
          <div v-for="(month, index) in months" :key="index"
               class="calendar-month"
               @click="selectMonth(index)">
            {{ month }}
          </div>
        </div>

        <!-- 📌 년 선택 모드 -->
        <div v-else-if="mode === 'year'" class="calendar-grid_year">
          <div v-for="year in years" :key="year"
               class="calendar-year"
               @click="selectYear(year)">
            {{ year }}
          </div>
        </div>
      </div>
    </div>
</template>

<script setup>
import { ref, computed, defineExpose, defineEmits } from "vue";
import dayjs from "dayjs";
import "dayjs/locale/ko"; // ✅ 한글 로케일 추가

dayjs.locale("ko"); // ✅ 전역 설정

const isOpen = ref(false);
const mode = ref("calendar"); // ✅ 기본 모드는 캘린더 (달력)

// ✅ 현재 선택된 날짜, 월, 연도
const selectedDate = ref(dayjs()); 
const currentMonth = ref(dayjs().startOf("month"));
const currentYear = ref(dayjs().year()); // ✅ 현재 연도 저장
const emit = defineEmits(["dateSelected"]); // ✅ 부모로 선택한 날짜 전달

// ✅ 캘린더 열기 (초기 날짜 설정 가능)
const openCalendar = (initialDate = dayjs()) => {
  isOpen.value = true;

  selectedDate.value = dayjs(initialDate, "YYYY-MM-DD").isValid()
    ? dayjs(initialDate, "YYYY-MM-DD")
    : dayjs();
  currentMonth.value = selectedDate.value.startOf("month");
  currentYear.value = selectedDate.value.year();
};

// ✅ 캘린더 닫기
const closeCalendar = () => {
  isOpen.value = false;
  mode.value = "calendar"; // ✅ 닫을 때 캘린더 모드로 초기화
};

// ✅ 날짜 선택
const selectDate = (date) => {
  selectedDate.value = date;
  emit("dateSelected", date.format("YYYY-MM-DD"));
  closeCalendar();
};

// ✅ 부모에서 접근 가능하도록 메서드 공개
defineExpose({ openCalendar });

// ✅ 월, 년도 표시
const formattedMonth = computed(() => currentMonth.value.format("YYYY년 MM월"));
const formattedYear = computed(() => currentYear.value + "년");

// ✅ 요일 배열
const daysOfWeek = ["일", "월", "화", "수", "목", "금", "토"];

// ✅ 현재 월의 모든 날짜 계산
const calendarDays = computed(() => {
  const startDay = currentMonth.value.startOf("month").startOf("week"); // **일요일 시작**
  const endDay = currentMonth.value.endOf("month").endOf("week");
  const days = [];
  let day = startDay;

  while (day.isBefore(endDay, "day")) {
    days.push({
      date: day,
      day: day.format("DD"),
      isOtherMonth: !day.isSame(currentMonth.value, "month"),
    });
    day = day.add(1, "day");
  }
  return days;
});

// ✅ 날짜 선택 여부
const isSelected = (date) => {
  return date.isSame(selectedDate.value, "day");
};

// ✅ 월 변경
const prevMonth = () => {
  currentMonth.value = currentMonth.value.subtract(1, "month");
};
const nextMonth = () => {
  currentMonth.value = currentMonth.value.add(1, "month");
};

// ✅ 📌 1년 증가 (월 선택 모드에서 사용)
const nextYear = () => {
    currentYear.value += 1;
    currentMonth.value = currentMonth.value.add(1, "year");
};

// ✅ 📌 1년 감소 (월 선택 모드에서 사용)
const prevYear = () => {
    currentYear.value -= 1;
    currentMonth.value = currentMonth.value.subtract(1, "year");
};


// ✅ 📌 "YYYY년 MM월" 클릭 시 월 선택 모드로 변경
const toggleMonthView = () => {
  mode.value = "month";
};

// ✅ 📌 "YYYY년" 클릭 시 년 선택 모드로 변경
const toggleYearView = () => {
  mode.value = "year";
};

// ✅ 📌 연도 변경 (← / → 버튼)
const prevDecade = () => {
  currentYear.value -= 12;
};
const nextDecade = () => {
  currentYear.value += 12;
};

// ✅ 📌 년도 범위 표시 (예: 2020 - 2031)
const yearRange = computed(() => {
  return `${currentYear.value - 6} - ${currentYear.value + 5}`;
});

// ✅ 📌 년도 선택 리스트 (12년씩 표시)
const years = computed(() => {
  const startYear = currentYear.value - 6;
  return Array.from({ length: 12 }, (_, i) => startYear + i);
});

// ✅ 📌 연도 선택 시 해당 연도로 이동 후 월 선택 모드로 변경
const selectYear = (year) => {
  currentYear.value = year;
  currentMonth.value = currentMonth.value.year(year);
  mode.value = "month";
};

// ✅ 📌 월 선택 모드에서 월 클릭 시 해당 월로 변경
const months = ["1월", "2월", "3월", "4월", "5월", "6월", "7월", "8월", "9월", "10월", "11월", "12월"];

const selectMonth = (index) => {
//   currentYear.value = year;
  currentMonth.value = currentMonth.value.month(index);
  mode.value = "calendar"; // ✅ 다시 캘린더 모드로 변경
};

// ✅ 📌 월 선택 모드 닫기
const closeMonthView = () => {
  mode.value = "calendar";
};
</script>


<style lang="scss" scoped>
/* ✅ 캘린더 스타일 */
.calendar-container {
  position: fixed;
  width: 300px;
  padding: 10px;
  border-radius: 10px;
  background: white;
  text-align: center;
  box-shadow: 0px 4px 10px rgba(0,0,0,0.2);
}


/* ✅ 헤더 */
.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 17px;
  padding: 10px;
}

.calendar-header .daymonth:hover{
   background: #E0E0E0;
   padding: 5px 1px 5px 1px;
}

.calendar-header button {
  border: none;
  background: transparent;
  font-size: 20px; /* 폰트 크기 증가 */
  padding: 8px 12px; /* 버튼 내부 여백 증가 */
  cursor: pointer;
}

/* ✅ 요일 스타일 */
.calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  font-weight: bold;
  padding-bottom: 5px;
}

/* ✅ 날짜 스타일 */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}

.calendar-day {
  padding: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s;
}

.calendar-day:hover {
  background: #f3f3f3;
  border-radius: 50%;
}

/* ✅ 월 선택 모드 */
.calendar-grid_month {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.calendar-month {
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s;
  border-radius: 10px;
  text-align: center;
  background-color: #f3f3f3;
}

.calendar-month:hover {
  background: #ff5252;
  color: white;
}

.calendar-grid_year {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.calendar-year {
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 10px;
  text-align: center;
  background-color: #f3f3f3;
}

.calendar-year:hover {
  background: #ff5252;
  color: white;
}


/* ✅ 오늘 날짜 스타일 */
.selected {
  background: #ff5252;
  color: white;
  border-radius: 50%;
}

/* ✅ 이번 달이 아닌 날짜 스타일 */
.faded {
  color: #ccc;
}

/* ✅ **일요일(빨간색)** */
.sunday {
  color: red;
}

/* ✅ **토요일(파란색)** */
.saturday {
  color: blue;
}
</style>
