<template>
  <div ref="gridContainer" class="tui-grid-wrapper"></div>
</template>

<script>
import { ref, onMounted, watch, nextTick } from "vue";
import Grid from "tui-grid";
import "tui-grid/dist/tui-grid.css";

// ✅ 스트라이프 테마 적용
Grid.applyTheme("striped");

export default {
  name: "TuiGridWrapper",
  props: {
    data: {
      type: Array,
      required: true
    },
    columns: {
      type: Array,
      required: true
    },
    bodyHeight: {
      type: Number,
      default: 400
    }
  },
  setup(props) {
    const gridContainer = ref(null);
    const gridInstance = ref(null);
    const isGridInitialized = ref(false); // ✅ Grid가 생성되었는지 확인

    // ✅ Grid 생성 함수
    const createGrid = () => {
      if (!gridContainer.value) {
        console.error("🚨 Grid 컨테이너가 존재하지 않음!");
        return;
      }
      if (!props.columns.length) {
        console.warn("⚠️ 컬럼 데이터가 없습니다. Grid 생성 대기...");
        return;
      }

      console.log("✅ Grid 생성 시작");

      gridInstance.value = new Grid({
        el: gridContainer.value,
        data: props.data || [],
        columns: props.columns.map(col => ({ ...col, headerText: col.header || col.headerText })), // ✅ header 적용 보장
        bodyHeight: props.bodyHeight,
        header: { height: 60 }, // ✅ 헤더 높이 설정
      });

      isGridInitialized.value = true; // ✅ Grid가 생성됨을 표시
      console.log("✅ Grid 인스턴스 생성됨:", gridInstance.value);
    };

    // ✅ Grid 업데이트 함수 (컬럼 변경 시)
    const updateGridColumns = () => {
      if (!gridInstance.value) return;

      nextTick(() => {
        console.log("✅ Grid 컬럼 업데이트 실행:", JSON.stringify(props.columns, null, 2));

        gridInstance.value.setColumns(props.columns.map(col => ({ ...col, headerText: col.header || col.headerText }))); // ✅ header 적용 보장
        gridInstance.value.refreshLayout(); // ✅ 레이아웃 업데이트
      });
    };

    // ✅ Grid 데이터 업데이트 함수 (데이터 변경 시)
    const updateGridData = () => {
      if (!gridInstance.value) return;

      nextTick(() => {
        console.log("✅ Grid 데이터 변경 감지, 업데이트 실행");
        gridInstance.value.resetData([...props.data]); // ✅ 새로운 데이터 적용
        gridInstance.value.refreshLayout(); // ✅ 레이아웃 업데이트
      });
    };

    // ✅ `onMounted()`에서 컬럼을 받아 Grid 생성
    onMounted(() => {
      nextTick(() => {
        console.log("📌 전달된 초기 컬럼:", JSON.stringify(props.columns, null, 2));

        if (props.columns.length > 0) {
          createGrid();
        } else {
          console.warn("⚠️ 초기 컬럼이 없어서 Grid 생성을 대기합니다.");
        }
      });
    });

    // ✅ `columns`이 준비되면 Grid를 생성 (or 업데이트)
    watch(
      () => props.columns,
      (newColumns) => {
        console.log("📌 새로운 컬럼 감지:", JSON.stringify(newColumns, null, 2));

        if (!newColumns.length) {
          console.warn("⚠️ 컬럼이 없어서 Grid 생성을 대기합니다.");
          return;
        }

        if (!isGridInitialized.value) {
          console.log("✅ Grid가 아직 없으므로 생성 중...");
          createGrid();
        } else {
          console.log("✅ 기존 Grid 컬럼 업데이트");
          updateGridColumns();
        }
      },
      { immediate: true, deep: true }
    );

    // ✅ `data`가 변경되면 Grid 업데이트 (중복 업데이트 방지)
    watch(
      () => props.data,
      (newData, oldData) => {
        if (!isGridInitialized.value) return;

        if (JSON.stringify(newData) !== JSON.stringify(oldData)) { // ✅ 데이터 변경 여부 체크
          updateGridData();
        } else {
          console.log("⚠️ 동일한 데이터 - 업데이트 생략");
        }
      },
      { deep: true }
    );

    return { gridContainer };
  }
};
</script>

<style lang="scss">
.tui-grid-wrapper {
  width: 100%;
  height: 400px;
  min-height: 300px;
}

/* ✅ Grid 전체 폰트 크기 변경 */
.tui-grid-container {
  font-size: 20px !important;
  font-family: 'HanSantteutDotum-Bold' !important;
  src: url('@/assets/fonts/HanSantteutDotum-Bold.ttf') format('truetype') !important;
  font-weight: normal !important;
  font-style: normal !important;
}

/* ✅ Grid 헤더 폰트 크기 변경 */
.tui-grid-header th {
  font-size: 20px !important;
  width: 150px !important;
  height: 50px !important; 
}

/* ✅ Grid 바디 셀 폰트 크기 변경 */
.tui-grid-body td {
  font-size: 20px !important;
  width: 150px !important;
  height: 50px !important;
}

</style>
