<template>
    <router-view @login_success="login_success"/>
</template>

<script>
  import { onMounted} from 'vue';
  import { useRouter } from 'vue-router';
  import axios from '@/axios.js'

export default {
  name: 'App',
  setup(){
    const router = useRouter();

    // ✅ 서버에서 세션을 확인하는 함수
    const checkSession = async () => {
      try {
        const response = await axios.get('/login/checklogin', { withCredentials: true });
        if (response.data.isAuthenticated) {
          router.push({ path: "/Index" }); // ✅ 메인 페이지로 이동
        } else {
          router.push({ name: 'loginPageView' }); // ✅ 세션이 없으면 로그인 페이지로 이동
        }
      } catch (error) {
        console.error("세션 확인 실패:", error);
        router.push({ name: 'loginPageView' });
      }
    };

    // ✅ 페이지가 로드될 때 로그인 상태 체크
    onMounted(() => {
      checkSession();
    });

    // ✅ 로그인 성공 시 호출되는 함수
    function login_success() {
      console.log("✅ 로그인 성공!");
      router.push({ path: "/Index" }); // ✅ 메인 페이지로 이동
    }
    
    return{
      login_success,
    }
  }
}
</script>

<style>

  html {
    overflow-y: scroll; /* 항상 세로 스크롤바를 유지해서 화면 이동 방지 */
  }
  html, body {
      height: 100%;  /* 화면 전체 높이를 부모로 설정 */
      margin: 0;     /* 기본 여백 제거 */
      padding: 0;
      top: 0;
      left: 0;
      box-sizing: border-box;  /* 여백과 테두리 포함하여 계산 */
  }
</style>
