import { createRouter, createWebHistory } from 'vue-router';
import LoginPage from '../views/LoginPage.vue';  // 홈 페이지 컴포넌트
import IndexPage from '../views/IndexPage.vue';  // 로그인 후 이동할 대시보드 컴포넌트



const routes = [
  { path: '/Login', name: 'loginPageView', component: LoginPage },
  { path: '/Index', name: 'IndexPageViews', component: IndexPage}
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
