import { ref, watch } from 'vue';
import { fetchHeadData, fetchPositions, fetchTeams,  fetchJikData} from '@/api/searchService';



/*0~5까지 USER_DETAIL
0 : 전체권한
1 : 권역권한
2 : 본부권한
4 : 팀권한
5 : 개인권한*/ 
/*권한 관련 변수 ExcelAuth(엑셀권한), WriteAuth(등록권한) */
export function useSearchCriteria(USER_DETAIL, sessionData) {
    const headList = ref([]); // ✅ 권역 리스트 추가
    const positionList = ref([]); // ✅ 본부 리스트 저장
    const timList = ref([]); // ✅ 팀 리스트 저장
    const jiklist = ref([]); // ✅ 직책 리스트 저장장

    const selectedHead = ref('');
    const selectedPosition = ref('');
    const selectedTim = ref('');
    const saKnm = ref(''); 

    const isHeadDisabled = ref(false);
    const isPositionDisabled = ref(false);
    const isTimDisabled = ref(false);
    const isSaKnmDisabled = ref(false);

    // ✅ 초기 선택값 설정 함수 (fetch된 데이터 저장)
    async function initializeSelections() {
        if (!USER_DETAIL.value) return;

        try {
            headList.value = await fetchHeadData() || []; // ✅ 권역 데이터 불러오기
            jiklist.value = await fetchJikData() || []; // ✅ 직책 데이터 불러오기
        } catch (error) {
            console.error("권역 데이터 불러오기 실패:", error);
            headList.value = [];
        }

        if (['0', '1', '2', '3'].includes(USER_DETAIL.value)) {
            selectedHead.value = sessionData.value.head;
            
            // ✅ fetchPositions() 실행 후 positionList에 저장
            positionList.value = await fetchPositions(sessionData.value.head);
            selectedPosition.value = sessionData.value.position;
        
            // ✅ fetchTeams() 실행 후 timList에 저장
            timList.value = await fetchTeams(sessionData.value.position);
        }

        if (['0', '1'].includes(USER_DETAIL.value)) {
            selectedTim.value = sessionData.value.tim;
        }

        if (['0'].includes(USER_DETAIL.value)) {
            saKnm.value = sessionData.value.saknm;
        }

        // ✅ 권한에 따른 선택 제한 설정
        switch (USER_DETAIL.value) {
        case '0': // 개인권한
            isHeadDisabled.value = true;
            isPositionDisabled.value = true;
            isTimDisabled.value = true;
            isSaKnmDisabled.value = true;
            break;
        case '1': // 팀권한
            isHeadDisabled.value = true;
            isPositionDisabled.value = true;
            isTimDisabled.value = true;
            isSaKnmDisabled.value = false;
            break;
        case '2': // 본부권한
            isHeadDisabled.value = true;
            isPositionDisabled.value = true;
            isTimDisabled.value = false;
            isSaKnmDisabled.value = false;
            break;
        case '3': // 권역권한
            isHeadDisabled.value = true;
            isPositionDisabled.value = false;
            isTimDisabled.value = false;
            isSaKnmDisabled.value = false;
            break;
        case '5': // 전체 권한
            isHeadDisabled.value = false;
            isPositionDisabled.value = false;
            isTimDisabled.value = false;
            isSaKnmDisabled.value = false;
            break;
        }
}   
    // ✅ `selectedHead` 변경 시 본부 리스트 불러오기
    watch(selectedHead, async (newHead) => {
        if (!newHead) {
            positionList.value = [];
            selectedPosition.value = '';
            timList.value = [];
            selectedTim.value = '';
            return;
        }

        positionList.value = await fetchPositions(newHead);
    });

    // ✅ `selectedPosition` 변경 시 팀 리스트 불러오기
    watch(selectedPosition, async (newPosition) => {
        if (!newPosition) {
            timList.value = [];
            selectedTim.value = '';
            return;
        }

        timList.value = await fetchTeams(newPosition);
    });


return {
    headList,//권역 데이터
    positionList,//본부 데이터
    timList, // 팀 데이터
    jiklist, // 직책 데이터
    selectedHead, //권역
    selectedPosition, //본부
    selectedTim, //팀
    saKnm, //팀장명
    isHeadDisabled, //권역권한
    isPositionDisabled, //본부권한
    isTimDisabled, //팀권한
    isSaKnmDisabled, //팀장명권한한
    initializeSelections // 초기 선택값 설정 함수
  };
}