<script setup>
import { ref } from "vue";
import { directives } from "vue-the-mask"; // ✅ v-mask 디렉티브 가져오기

const jumin = ref("");
</script>

<template>
  <input 
    v-model="jumin"
    v-mask="'######-#######'"
    placeholder="000000-0000000"
    @input="jumin = jumin.replace(/\D/g, '')" 
  />
</template>


<script>
export default {
  directives, // ✅ vue-the-mask의 디렉티브 등록 필수!
};
</script>
