<template>
    <div class="container">
         <figure style="margin-top: 0px;"><img src="@/assets/Img/login/main_logo_s.png" alt="로고"></figure>
         <div class="loginBox">
             <figure style="margin-top: 15px; width: 350px;"><img  style="width: 100%; " src="@/assets/Img/login/text_log01.png" alt=""></figure>
             <div class="flexBox">
                <img style="margin-bottom: 15px;" src="@/assets/Img/login/icon_key.gif" alt=""/>
                <div class="inputs">
                    <!-- 아이디 입력 박스 -->
                    <div class="login_input">
                        <img src="@/assets/Img/login/ar_01.gif" alt=""/>
                        <img src="@/assets/Img/login/img_id.png" alt="">
                        <input style="margin-left: 15px;" type="text" @input="user_Id = $event.target.value"/>
                        <label for="save_Id_Label">
                            <input style="margin-left: 40px;" type="checkbox" id="save_Id_Label"/><span>아이디 저장</span>
                        </label>
                    </div>
                    <!-- 비밀번호 입력 박스 -->
                    <div class="login_input">
                        <img src="@/assets/Img/login/ar_01.gif" alt=""/>
                        <img src="@/assets/Img/login/img_pw.png" alt="">
                        <input style="margin-left: 15px;" type="password" @input="user_Pw = $event.target.value"/>
                        <button style="margin-left: 40px; border: none; padding: 0;" @click="fnLogin">
                            <img src="@/assets/Img/login/bt_login.png" alt="Login" style="width: 100%; height: 100%; margin: 0;">
                        </button>
                    </div>
                </div>
             </div>
         </div>
    </div>

</template>

<style scoped>
    /* container의 중앙 배치 및 브라우저 최대 높이 차지 */
    .container {
        display: flex;
        flex-direction: column;   /* 세로로 배치 */
        justify-content: center;  /* 세로 중앙 정렬 */
        align-items: center;      /* 가로 중앙 정렬 */
        height: 100vh;            /* 정확히 100vh 크기 */
        width: 100%;
    }

    /* loginBox를 container의 중앙에 배치 */
    .container .loginBox {
        display: flex;               /* flexbox 사용 */
        flex-direction: column;      /* 세로로 배치 */
        align-items: center;
        height: 350px; /* 고정 높이 설정 */
        width: 900px; /* 고정 너비 설정 */
        border: 1px solid black;
        background-image: url('../assets/Img/login/log_tb_bg.png');
        background-size: 100% 100%; /* 이미지가 요소 크기에 맞게 왜곡 없이 조정 */
        background-position: center; 
        background-repeat: no-repeat; 
    }

    .flexBox {
        display: flex;               /* flexbox 사용 */
        flex-direction: row;         /* 가로로 배치 */
        justify-content: center;     /* 가로 중앙 정렬 */
        align-items: center;         /* 세로 중앙 정렬 */
        width: 100%;
        margin-right: 50px;
        margin-top: 30px;
    }

    .inputs {
        display: flex;
        flex-direction: column;      /* 세로로 배치 */
        align-items: flex-start;     /* 왼쪽 정렬 */
        margin-left: 10px;            /* 왼쪽 간격 추가 */
    }

    .login_input {
        display: flex;
        flex-direction: row;
        align-items: center; /* 세로 중앙 정렬 */
        margin-bottom: 15px;  /* 아이디와 비밀번호 간격 설정 */
    }

    .login_input img {
        height: 15px;
        margin-right: 10px;  /* 이미지와 텍스트 간격 설정 */
    }

   
</style>
<script>
    import '../assets/Css/main.css'; 
    import axios from '@/axios.js'; // axios 인스턴스 가져오기
    import { ref } from 'vue';
    export default{
        name : "loginPageViews",
        setup(props, { emit }){
            const user_Id = ref('');
            const user_Pw = ref('');

            async function fnLogin(){
                if(user_Id.value === ''){
                    alert('ID를 입력하세요.')
                    return;
                }

                if (user_Pw.value === '') {
                    alert('비밀번호를 입력하세요.')
                    return;
                }

                try{
                    const response = await axios.post('/login/login', {
                        user_Id : user_Id.value,
                        user_Pw : user_Pw.value
                    }, { withCredentials: true });   
                    // 로그인 성공 시 토큰 저장
                    const token = response.data.token;
                    localStorage.setItem('auth_token', token);
                    // 로그인 성공 메시지
                    alert('로그인 성공했습니다.');

                    // 부모 컴포넌트로 이벤트 전달
                    emit('login_success');
                } catch(error){
                    alert(error.response.data.Message);
                }
            }

            return {
                user_Id,
                user_Pw,
                fnLogin,
            };
        }
    }
</script>
