<template>
  <Teleport to="body">
    <div v-if="isOpen2" class="modal-overlay">
      <div 
        class="modal-content" 
        ref="modalRef2"
        @click.stop
        :style="{ top: position.top + 'px', left: position.left + 'px' }"
      >
        <div class="modal-header" @mousedown="startDrag">
          <h8>사원찾기</h8>
          <button @click="closeModal2">✖</button>
        </div>

        <div class="search-container">
          <h8 class="search-title">팀장 검색</h8>
          <div class="fiter-container">
            <table class="filter-table">
              <tr>
                <td class="lable">팀장명</td>
                <td class="left-border"><input class="yellow" v-model="SA_KNM"/></td>
              </tr>
            </table>
            <div class="button-group">
              <button class="search-btn" @click="Select_Induce">▣ 조회</button>
            </div>
          </div>
          
        </div>
        
        <div class="Induce-container">
          <table class="Induce-table">
            <colgroup>
              <col v-for="n in 4" :key="n" :style="{ width: (100 / 4) + '%' }" />
            </colgroup>
            <thead>
              <tr>
                <th v-for="col in columnDefs" :key="col.field">
                  {{ col.headerName }}
                </th>
              </tr>
            </thead>
          </table>
          <div class="scrollable-body">
            <table class="Induce-table">
              <colgroup>
                <col v-for="n in 4" :key="n" :style="{ width: (100 / 4) + '%' }" />
              </colgroup>
              <tbody>
                <tr v-for="(row, index) in rowData" :key="index" @dblclick="selectrow(row)">
                  <td v-for="col in columnDefs" :key="col.field">
                    {{ row[col.field] }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
  
</template>

<script setup>
import { ref, defineProps, defineEmits} from "vue";
import axios from '@/axios.js'

// eslint-disable-next-line
const props = defineProps({isOpen2: Boolean}) 
const emit = defineEmits(["update:isOpen2","select-induce"]);

const SA_KNM = ref(''); //팀장명
const columnDefs = ref([
  { field: 'POSITION_NM', headerName: '본부명' },
  { field: 'TIM_NM', headerName: '팀명' },
  { field: 'SA_CD', headerName: '팀장코드' },
  { field: 'SA_KNM', headerName: '팀장명' },
]);

const rowData = ref([]);

//모달 닫기
const closeModal2 = () => {
  SA_KNM.value = '';
  rowData.value = [];
  emit("update:isOpen2", false);
  console.log('모달닫힘');
};


// 드래그 관련 상태
const modalRef2 = ref(null);
const position = ref(
  { 
    top: window.innerHeight / 2 - 310, 
    left: window.innerWidth / 2 - 200,
  });
let drag = { active: false, offsetX: 0, offsetY: 0 };

// 드래그 시작
const startDrag = (event) => {
  drag.active = true;
  drag.offsetX = event.clientX - modalRef2.value.getBoundingClientRect().left;
  drag.offsetY = event.clientY - modalRef2.value.getBoundingClientRect().top;

  window.addEventListener("mousemove", onDrag);
  window.addEventListener("mouseup", stopDrag);
};

// 드래그 중
const onDrag = (event) => {
  if (!drag.active) return;
  position.value.top = event.clientY - drag.offsetY;
  position.value.left = event.clientX - drag.offsetX;
};

// 드래그 종료
const stopDrag = () => {
  drag.active = false;
  window.removeEventListener("mousemove", onDrag);
  window.removeEventListener("mouseup", stopDrag);
};






async function Select_Induce(){
  if(SA_KNM.value === ''){
    alert('팀장명을 입력해주세요');
    return;
  }

  try {
      const response = await axios.get('/INSUR9101/Get_InDuce', {
        params: { saknm: SA_KNM.value }
      })
      console.log('유치자 로딩 성공:', response)
      rowData.value = response.data;
    } catch (error) {
      console.error('유치자 로딩 실패', error)
    }
}



function selectrow(row){
  console.log("클릭함");
  emit("select-induce",{INDUCE_NM :row.SA_KNM,INDUCE_CD : row.SA_CD});
  closeModal2();
}


</script>

<style>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
/* 팝업창 */
.modal-content {
  position: fixed;
  top: 50%;
  right: 60%;
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  cursor: grab;
  width: 350px; /* ✅ 기존보다 더 크게 설정 */
  height: 600px; /* ✅ 높이도 더 크게 조정 */
}
/* 팝업 헤더 */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  cursor: move;
}
.modal-header button {
  border: none;
  background: none;
  font-size: 16px;
  cursor: pointer;
}
.filter-table{
  width: 60%;
  border-collapse: collapse;
  border-spacing: 0;
  margin-top: 10px;
  
}

.filter-table td,th{
  height: 35px;
  border-top: 1px solid black;
  border-bottom: 1px solid rgb(210,210,210);
}

.filter-table input{
  width: 99%;
  height: 100%;
  border: none;
}

.fiter-container{
  display:flex;
  align-items: center;
  justify-content: space-between; /* ✅ 테이블과 버튼을 양쪽 끝으로 배치 */
}
.button-group {
  display: flex;
  flex-direction: column; /* 버튼을 세로로 배치 */
}

.button-group button{
  height: 35px;
  width: 100px;
  margin-top: 10px;
} 

.left-border{
  border-left: 1px solid rgb(210,210,210);
}

.lable{
  text-align: center;
  font-size: 12px;
  background-color: rgb(242, 242, 242);
  width: 30%;
}

.yellow{
  background-color: rgb(255,255,166);
}

.Induce-table{
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed; /* 테이블 레이아웃 고정 */
}

/* 헤더 고정 */
.Induce-table thead {
  width: 100%;
}

.Induce-table th{
  font-size: 12px;
  height: 35px;
  background-color: rgb(242, 242, 242);
  border-top :2px solid black;
}

/* 내용 스크롤 가능하게 */
.Induce-table tbody {
  max-height: 300px; /* ✅ 원하는 높이 설정 */
  overflow-y: auto;
  width: 100%;
}

.Induce-table td {
  font-size: 10px;
  height: 30px;
  text-align: center;
  border-right: 1px solid rgb(210, 210, 210);
  border-bottom: 1px solid rgb(210, 210, 210);
  word-break: break-word; /* 긴 텍스트 줄바꿈 */
}

.Induce-table th,
.Induce-table td{
  width: 25%;
}

.Induce-container {
  width: 100%;
  margin-top: 10px;
}

.scrollable-body {
  max-height: 430px;
  overflow-y: auto;
  display:block;
}


</style>