const selectedCompany = ref('')
const selectedjik = ref('')
const selectedStatus = ref('')


//버튼 이벤트 메서드
async function onSearch() {
  try {
    const response = await axios.get('/INSUR9103/Get_Sawaon', {
      params: {
        hq_cd: selectedCompany.value,
        head_cd: selectedHead.value,
        position_cd: selectedPosition.value,
        saKnm : saKnm.value,
        tim_cd: selectedTim.value, 
        buty_nm: selectedjik.value,
        user_detail : USER_DETAIL,
        out_yn : selectedStatus.value
      },withCredentials: true
    })
    rowData.value = response.data;
  } catch (error) {
    console.error('조회실패:', error)
  }
}

function changeTab(index) {
  activeTab.value = index;
}

//왼쪽 테이블 기본 조회 테이블 더블 클릭 시
async function RowDoubleClick(row){
  try {
    const sa_cd = row.SA_CD;
    const response = await axios.get('/INSUR9103/Get_UserDetail_info', {
      params: {
        sa_cd : sa_cd,
        insur_gubun : selectedInsur.value
      }
    })

    //그리드 데이터
    basicInfo.value = ([]);
    family_rowData.value = ([]);
    education_rowData.value = ([]);
    work_details_rowData.value = ([]);
    insur_code_rowData.value = ([]);
    warranty_details_rowData.value = ([]);
    credit_check_rowData.value = ([]);

    //클릭 데이터 초기화
    family_clickData.value = ([]);

    //기본인적사항
    basicInfo.value =  response.data.basicInfo_data;
    //가족사항
    family_rowData.value = response.data.familyInfo_data;
    //학력사항
    education_rowData.value = response.data.educationInfo_data;
    //전근무사항
    work_details_rowData.value = response.data.workInfo_data;
    //보험사별코드
    insur_code_rowData.value = response.data.insurcode_data;
    //보증내역
    warranty_details_rowData.value = response.data.warranty_data;
    //신용조회내역
    credit_check_rowData.value = response.data.creditcheck_data;

  } catch (error) {
    console.error('조회실패:', error)
  }
}

