  position: relative; /* ✅ 자식 요소의 position: sticky를 올바르게 적용 */
  clip-path: inset(0px);
}

/* 🔹 검색 컨테이너가 부모의 너비를 꽉 채우도록 설정 */
.search-container {
  width: 100%;
  height: 80px;
  padding: 3px;
  box-sizing: border-box; /* padding을 포함하여 너비 계산 */
}

/* 🔹 검색 테이블 스타일 */
.search-table {
  width: 100%; /* 부모 컨테이너에 맞춤 */
  border-collapse: collapse; /* 셀 사이의 간격 제거 */
  table-layout: fixed; /* 각 열의 크기를 균등하게 맞춤 */
