}

/* 🔹 테이블 헤더(th) 및 데이터 셀(td) 스타일 */
.search-table th,
.search-table td {
  width: 12.5%; /* 100% / 8개 열 = 12.5% */
  height: 50px;
  text-align: center; /* 텍스트 가운데 정렬 */
  vertical-align: middle; /* 수직 가운데 정렬 */
  border-bottom: 2px solid rgb(210, 210, 210); /* 셀 테두리 */
  white-space: nowrap; /* 줄바꿈 방지 */
  overflow: hidden; /* 내용이 넘칠 경우 숨김 */
  position: relative; /* 내부 요소의 절대 위치를 위한 기준 */
  font-size: 20px;
}


/* 🔹 입력 필드, 셀렉트 박스 크기 고정 */
.search-table select,
.search-table input {
  width: 100%; /* 부모(td) 크기에 맞춤 */
  height: 100%; /* 부모(td) 크기에 맞춤 */
  border: none; /* 기본 테두리 제거 */
  outline: none; /* 선택 시 외곽선 제거 */
  box-sizing: border-box; /* padding 포함하여 크기 유지 */
  padding: 5px; /* 내부 여백 */
  font-size: 20px; /* 글자 크기 */
  text-align: center; /* 🔹 입력된 텍스트 가운데 정렬 */
}

/* 🔹 td 내부의 select, input 요소가 td 크기와 완벽하게 일치하도록 조정 */
.search-table td {
  position: relative; /* 내부 요소의 절대 위치를 위해 설정 */
}

/* 🔹 select와 input 요소를 td 내부에 완전히 맞춤 */
.search-table select,
.search-table input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%; /* 부모(td)와 동일한 크기 */
  height: 100%; /* 부모(td)와 동일한 크기 */
  text-align: center; /* 🔹 입력 필드 내부의 텍스트 가운데 정렬 */
}

/* 🔹 Select 요소 기본 스타일 제거 (일부 브라우저에서 적용) */
.search-table select {
  // appearance: none; /* 기본 화살표 제거 */
  background-color: transparent; /* 배경 투명 */
  text-align: center; /* 🔹 드롭다운 텍스트 가운데 정렬 */
}

/* 🔹 input 요소의 배경을 투명하게 유지 */
.search-table input {
  background-color: transparent; /* 배경색 투명 */
}

