<template>
    <div class="Headr_Container flex_Center">
        <div class="flex_Right" style="width: 100%;">
            <button @click="$emit('HomeChange')" style="width: 200px; margin-right: 5px;">Home</button>
            <button @click="$emit('SideBoolChange')" style="width: 200px; margin-right: 5px;">Side</button>
            <button style="width: 200px; margin-right: 5px; background-color: transparent;">비밀번호 변경</button>
            <button @click="logOut" style="width: 100px; background-color: black; color: white;">로그아웃</button>
        </div>
    </div>
</template>
<script>
import { useRouter } from 'vue-router';
import axios from '@/axios.js'

export default {
    name : "HeaderComponent",
    setup(){
        const router = useRouter();

        async function logOut() {
            try {
                const response = await axios.post("/login/logout", {}, { withCredentials: true });

                if (response.data.success) {
                    console.log("✅ 로그아웃 성공!");
                    localStorage.removeItem('isAuthenticated');
                    router.push({ name: 'loginPageView' });
                }
            } catch (error) {
                console.error("🚫 로그아웃 실패:", error);
            }
        }

        return{
            logOut,
        }
    }
}
</script>
<style lang="scss" scoped>
    .Headr_Container {
        height: 50px;
        margin: 0;
        width: 100%;
    }
    .Headr_Container button{
        height: 45px;
        margin-top: 20px;
    }

    *{
        @extend .basic_font_Bold;
    }
</style>