<template>
  <div class="scale_Size_Change">
    <div class="flex_left Main_Container">
      <div class="flex_left" style="width: 100%;">
        <Header 
          @HomeChange="HomeChange"
          @SideBoolChange="SideBoolChange"
        />
        <Navbar 
          @NavbarmainMenuClick="NavbarmainMenuClick"
          @NavbarSubMenuClick="NavbarSubMenuClick"
        />
        <div style="width: 2600px; position: relative;">
          <Sidebar 
            :menuData="menuData" 
            :subIndex="subIndex"
            :subSubIndex="subSubIndex"
            v-show="sideBar_Bool"
            :style="{ minHeight: `${contentHeight}px` }" 
            @SidebarSubMenuClick="SidebarSubMenuClick"
          />
          <div ref="tabContentRef" :style="sideBar_Bool ? { marginLeft: '300px' } : {}">
            <Tab
              :tabs="tabs" 
              :activeTab="activeTab"
              @changeTab="changeTab"
              @closeTab="closeTab"
            />
          </div>
        </div>
      </div>
      <div :style="sideBar_Bool ? { marginLeft: '300px' } : {}">
        <keep-alive>
          <component 
            :is="getPageComponent(currentTab) || 'div'" 
            :key="currentTab.key"
          />
        </keep-alive>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import Header from '../components/Header.vue';
import Navbar from '../components/Navbar.vue'; 
import Sidebar from '../components/Sidebar.vue'; 
import Tab from '../components/Tab.vue'; 

// Webpack에서 동적 모듈을 로드하기 위한 require.context 사용
const pages = require.context('@/views', true, /\.vue$/);


export default {
  name: 'IndexPageViews',
  components: {
    Header,
    Navbar,
    Sidebar,
    Tab,
  },
  setup() {
    const sideBar_Bool = ref(false);
    const subIndex = ref(-1);
    const subSubIndex = ref(-1);
    const menuData = ref([]);
    const contentHeight = ref(0);
    const tabContentRef = ref(null);
    const tabs = ref([{ name: 'Home', path: './MainPage.vue', key: 0, menuData: [], subIndex : -1,  subSubIndex : -1}]); // 기본 탭 (홈)
    const activeTab = ref(0);
    const currentTab = ref(tabs.value[0]); // 현재 활성화된 탭
    const lastDateKey = ref(""); // 🔹 마지막으로 저장된 날짜

    // 기본적으로 'Home' 페이지 탭을 설정
    function HomeChange() {
      const existingTab = tabs.value.findIndex(t => t.path === './MainPage.vue');
      if (existingTab === -1) {
        tabs.value.unshift({ name: 'Home', path: './MainPage.vue', key: getDateBasedKey(), menuData: [], subIndex : -1,  subSubIndex : -1});
        activeTab.value = tabs.value.length - 1;
      } else {
        activeTab.value = existingTab;
      }
      currentTab.value = tabs.value[activeTab.value];
    }

    function SideBoolChange() {
      if (menuData.value.length < 1) return;
      sideBar_Bool.value = !sideBar_Bool.value;
    }

    function NavbarmainMenuClick(menuData_prop) {
      menuData.value = menuData_prop.returnSideMenu.value;
      sideBar_Bool.value = true;
    }

    function NavbarSubMenuClick(Menus) {
      const subSubPath = Menus.Path;
      const subSubMenuName = Menus.SubMenuName;

      const subSubMenu = { SubMenuName: subSubMenuName, path: subSubPath, menuData: Menus.returnSideMenu.value, subIndex : Menus.subIndex , subSubIndex : Menus.subSubIndex};
      sideBar_Bool.value = true;

      InsertTab(subSubMenu);
    }

    function SidebarSubMenuClick(SubMenu) {
      InsertTab(SubMenu);
    }

    function InsertTab(SubMenu) {
      const SubMenuPathArray = SubMenu.path.split('/').map(part => part.trim());
      const SubMenuPath = SubMenuPathArray.join('/')

      const existingTab = tabs.value.findIndex(t => t.path === SubMenuPath);

      if (existingTab === -1) {
        const newTab = { 
          name: SubMenu.SubMenuName, 
          path: SubMenuPath, 
          key: getDateBasedKey(), // 🔹 날짜 기반 key 사용
          menuData : SubMenu.menuData,
          subIndex : SubMenu.subIndex,
          subSubIndex : SubMenu.subSubIndex
        };

        tabs.value.push(newTab);
        activeTab.value = tabs.value.length - 1;
      } else {
        activeTab.value = existingTab;
      }
      currentTab.value = tabs.value[activeTab.value]; // 🔹 탭 전환
      menuData.value = SubMenu.menuData;
      subIndex.value = SubMenu.subIndex;
      subSubIndex.value = SubMenu.subSubIndex;
    }

    function closeTab(index) {
      const isActive = activeTab.value === index;
      tabs.value.splice(index, 1);

      if (isActive) {
        if (tabs.value.length > 0) { 
          activeTab.value = Math.max(0, activeTab.value - 1);
          currentTab.value = tabs.value[activeTab.value]; // 🔹 기존 key 유지 (캐싱 활용)
          if(tabs.value[activeTab.value].name != "Home"){
            menuData.value = tabs.value[activeTab.value].menuData;
          }
          subIndex.value = tabs.value[activeTab.value].subIndex;
          subSubIndex.value = tabs.value[activeTab.value].subSubIndex;
        } else {
          tabs.value = [{ name: 'Home', path: './MainPage.vue', key: getDateBasedKey(),  menuData: []}];
          currentTab.value = tabs.value[0]; // 🔹 Home 탭으로 이동
        }
      }
    }

    function changeTab(index) {
      activeTab.value = index;
      currentTab.value = tabs.value[index]; // 🔹 기존 key 유지 (리렌더링 방지)
      if(tabs.value[index].name != "Home"){
        menuData.value = tabs.value[index].menuData;
        subIndex.value = tabs.value[index].subIndex;
        subSubIndex.value = tabs.value[index].subSubIndex;
      } 
    }

    const getPageComponent = (currentTab) => {
      const { name, path} = currentTab;
      let filePath = '';
      if (name === 'Home') {
        filePath = './MainPage.vue';
        return pages(filePath).default; 
      }

      if (path) {
        filePath = `./insur/${path}.vue`;
      }

      try {
        return pages(filePath).default;
      } catch (error) {
        return null;
      }
    };

    function getDateBasedKey() {
      const now = new Date();
      const dateStr = `${now.getFullYear()}${(now.getMonth() + 1).toString().padStart(2, '0')}${now.getDate().toString().padStart(2, '0')}`
                    + `-${now.getHours().toString().padStart(2, '0')}${now.getMinutes().toString().padStart(2, '0')}${now.getSeconds().toString().padStart(2, '0')}`;

      if (lastDateKey.value.split('-')[0] !== dateStr) {
        lastDateKey.value = `${dateStr}-0`; 
      }

      const currentNum = parseInt(lastDateKey.value.split('-')[1] || 0) + 1;
      lastDateKey.value = `${dateStr}-${currentNum}`;

      return lastDateKey.value;
    }


    return {
      NavbarmainMenuClick,
      NavbarSubMenuClick,
      menuData,
      subIndex,
      subSubIndex,
      sideBar_Bool,
      changeTab,
      closeTab,
      tabs,
      activeTab,
      SidebarSubMenuClick,
      contentHeight,
      tabContentRef,
      SideBoolChange,
      HomeChange,
      currentTab, 
      getPageComponent,
    };
  }
};
</script>

<style>
  @import '../assets/Css/main.css';
  body {
    transition: padding-right 0.2s ease, transform 0.3s ease;
}

/* 컨테이너는 원래 너비 유지 */
.Main_Container {
    width: 2900px; /* 기본 크기 유지 */

}

.scale_Size_Change {
    zoom: 1; /* 기본 크기 */
    transform-origin: center top; /* 중앙을 기준으로 확대 */
    width: 2900px; /* 컨테이너 너비 고정 */
    display: flex;
    justify-content: center; /* 가로 중앙 정렬 */
    align-items: flex-start; /* 위쪽 정렬 */
    margin: 0 auto;
}


/* 4K 해상도 이상 (넓은 화면) */
@media screen and (min-device-width: 2560px) {
    .scale_Size_Change {
      zoom: 1; 
    }
}

/* 데스크탑 (1440px ~ 2559px) */
@media screen and (max-device-width: 2559px) {
    .scale_Size_Change {
      zoom: 0.553; 
    }
}

/* 노트북 (1024px ~ 1439px) */
@media screen and (max-device-width: 1439px) {
    .scale_Size_Change {
      zoom: 0.393;
    }
}

/* 태블릿 (768px ~ 1023px) */
@media screen and (max-device-width: 1023px) {
    .scale_Size_Change {
      zoom: 0.293; 
    }
}


</style>
 